import { OrderStatusEnum } from './../../enums/OrderStatusEnum';
import { Component, OnInit, ViewChild } from '@angular/core';
import { DatatableComponent } from '@swimlane/ngx-datatable';
import { GetPopularChemistService } from '../../services/GetPopularChemistService';
import { AddCategoryService } from '../../services/AddCategoryService';
import { GetChemistDataService } from '../../services/GetChemistDataService';
import { OrderService } from '../../services/OrderService';
import swal from 'sweetalert2';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { OrderModel } from '../../models/order.Model';
import { AddAreaService } from '../../services/AddAreaService';
import { AddressModel } from '../../models/address.Model';
import { GetOrderService } from '../../services/GetOrderService';


declare var $: any;

@Component({
  selector: 'edit-order',
  templateUrl: './edit-order.component.html',
  styleUrls: [
    './edit-order.component.scss',
    '../../../../assets/icon/icofont/css/icofont.scss'
  ],
  providers: [AddCategoryService, GetPopularChemistService, GetOrderService, AddAreaService]
})
export class EditOrderComponent implements OnInit {

  public editingOrder: OrderModel;

  public gotAddress: boolean = false;

  public OrderItemsAvailable: boolean = false;

  public AddressDetails: AddressModel;

  public OrderId: any;

  public showUpdateStatusBtn: boolean = false;

  public showUpdateStatusLoader: boolean = false;

  public newStatus: any;

  public OrderStatuses = {
    pending: 100,
    confirmed: 200,
    ontheway: 300,
    notshipped: 400,
    shipped: 500,
    tobedelivered: 600,
    canceled: 700,
    completed: 800,
    returned: 900,
    delivered: 1000
  }

  constructor(public _addCategoryService: AddCategoryService,
    public route: Router,
    public activatedRoute: ActivatedRoute,
    public _orderService: GetOrderService,
    public _addAddressService: AddAreaService) {

    this.AddressDetails = new AddressModel();

  }


  getNewStatus(elem) {
    this.showUpdateStatusBtn = true;
    this.newStatus = elem;

    for (var key in this.OrderStatuses) {
      if (key == this.newStatus) {

        this.newStatus = this.OrderStatuses[key];

      }
    }

  }

  updateStatusOfThisOrder() {
    this.showUpdateStatusBtn = false;
    this.showUpdateStatusLoader = true;

    this._orderService.UpdateOrder(this.OrderId, this.newStatus).subscribe(a => {
      console.log(a);

      if (a.code == 200) {
        this.showUpdateStatusBtn = false;
        this.showUpdateStatusLoader = false;
        this.editingOrder = a.data;
        swal(
          'Updated!',
          'Order status has been updated.',
          'success'
        );
      } else {
        swal(
          'Not Updated!',
          a.message,
          'error'
        );
      }
    }, err => {
      var obj = JSON.parse(err._body);
      this.showUpdateStatusBtn = false;
      this.showUpdateStatusLoader = false;
      swal(
        'Updated!',
        obj.message,
        'error'
      );
    })
  }

  getFullAddress(AddressId) {



    this._addAddressService.getUserAddressById(AddressId).subscribe(a => {

      console.log(a);

      if (a.code == 200) {

        this.gotAddress = true;
        this.AddressDetails = a.data;
      }


    })
  }



  getOrderDetails(orderId) {

    this._orderService.getOrderByOrderId(orderId).subscribe(orderResponse => {


      this.editingOrder = orderResponse.data;

      console.log(this.editingOrder);

      this.getFullAddress(this.editingOrder.AddressId);

      var myDate = new Date(parseFloat(this.editingOrder.CreatedOnUTC) * 1000);

      this.editingOrder.CreatedOnUTC = myDate.toLocaleString();




      for (var prop in this.OrderStatuses) {

        if (this.editingOrder.OrderStatus == this.OrderStatuses[prop]) {

          this.editingOrder.OrderStatus = prop;
          $('#statusOfOrder option[value="' + this.editingOrder.OrderStatus + '"]').prop('selected', true);

        }
      }


      this.OrderItemsAvailable = true;
    })
  }

  ngOnInit() {

    this.activatedRoute.queryParams.subscribe((params: Params) => {

      this.OrderId = params['id'];

      this.getOrderDetails(this.OrderId);

    })

  }
}



